.profile {
  display: flex;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  grid-row-gap: 26px;
  row-gap: 26px;
  align-items: center;
}

@media (max-width: 917px) {
  .profile {
    flex-direction: column;
    max-width: 581px;
    margin: 0 auto;
  }
}

.profile__cover {
position: relative;
background: none;
border: none;
}


.profile__cover::before {
  content: "";
  background-color: #000;
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: opacity 0.2s;
}

.profile__cover:hover::before {
  opacity: 0.8;
  cursor: pointer;
}
.profile__pen {
  position: absolute;
  top: calc(50% - 13px);
  left: calc(50% - 13px);
  opacity: 0;
  transition: opacity 0.3s;
}

.profile__cover:hover .profile__pen {
  opacity: 1;
  cursor: pointer;
}
.profile__avatar {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
}
.profile__name-edit {
  display: flex;
  grid-column-gap: 18px;
  -webkit-column-gap: 18px;
          column-gap: 18px;
  justify-content: flex-start;
  align-items: flex-end;
}

@media (max-width: 618px) {
  .profile__name-edit {
    justify-content: center;
    max-width: 282px;
    min-width: 214px;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
  }
}

.profile__name {
  max-width: 508px;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.143;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media (max-width: 917px) {
  .profile__name {
    max-width: 539px;
  }
}

@media (max-width: 618px) {
