  width: 24px;
  height: 24px;
  background: none;
  border: 1px solid #ffffff;
  margin-bottom: 8.5px;
  transition: opacity 0.2s;
}

.profile__edit-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

