.popup {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s linear;
}

.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-margin-start: 0;
          margin-inline-start: 0;
  -webkit-margin-end: 0;
          margin-inline-end: 0;
}

@media (max-width: 618px) {
  .popup__container {
    width: 282px;
    grid-gap: 16px;
    gap: 16px;
  }
}

.popup__close-button {
  align-self: flex-end;
  width: 32px;
  height: 32px;
  padding: 0;
  background: none;
  transition: opacity 0.2s;
  border: none;
  -webkit-transform: translateX(40px);
          transform: translateX(40px);
}

.popup__close-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media (max-width: 618px) {
